@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Text&display=swap);
nav {
  padding: 20px 0;
}

.nav-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  z-index: 3;
}

.nav-container {
  display: flex; 
  justify-content: space-between;
}

.hamburger {
  margin-left: auto;
}

nav ul {
  display: none;
}

.nav-show {
  display: flex; 
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 79px;
  left: 0;
  width: 100%;
  height: calc(100% - 80px);
  background: #2B282F;
  color: #fff;
  z-index: 1;
}

nav ul li {
  margin: 30px 0;
}

.btn {
  background: transparent;
  padding: 10px 15px;
  border: 2px solid;
}
.btn-dark {
  border-color: black;
  color: purple;
}


.showcase {
  width: 100%;
  color: #FFFFFD;
  background-color: #2C2640;
}

.showcase hr {
  display: none;
}


.showcase-container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: #2C2640; */
}

.showcase-col-one {
  display: flex;
  flex-direction: column;
}

.showcase-img {
  width: 100%;
}

.showcase-img img {
  /* img itself needs to be at width 100%, not its parent div */
  width: 100%;
}

.showcase-body {
  /* background styling */
  background-image: url(/insure-lp-fem/static/media/bg-pattern-intro-left-mobile.562149a8.svg);
  background-repeat: no-repeat;
  background-size: 40% 55%;
  background-position: top left;
  text-align: center;
  margin-bottom: 100px;
}

.showcase-body h1 {
  font-size: 3rem;
}

.showcase-col-two {
  /* col for desktop with image */
  display: none;
}

.h1 {
  font-size: 3rem;
}

.showcase-body a {
  text-transform: uppercase;
  border: 2px solid #fff; 
  padding: 10px;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}

.showcase-body a:hover {
  background-color: #fff; 
  color: #2C2640;
}


@media (min-width:769px) {

  hr {
    display: block;
    width: 100px;
  }
  
  .showcase-col-one {
    width: 100%;
  }

  .showcase-col-one img {
    display: none;
  }

  .showcase-col-two {
    display: block;
  }
}
.features {
  width: 100%;
  padding: 100px 0;
}

.features-container {
  width: 80%;
  margin: 0 auto;
}

.features-content {
  text-align: center;
}

.features-content h1 {
  font-size: 2.5rem;
  margin-bottom: 60px;
}

.features-content hr {
  width: 100px;
  border: 1px solid;
  color: grey;
}

.card-deck {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card {
  width: 75%;
  margin-bottom: 40px;
}

/* bg svgs */

.bg-intro-right {
  position: absolute;
  top: 1000px;
  right: 0px;
}


.cta {
  width: 100%;
  align-items: center;
  color: #fff;
}

.cta-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center; 
  width: 90%;
  margin: 0 auto;
  height: 400px;
  background-color: #2C273F;
  background-image: url(/insure-lp-fem/static/media/bg-pattern-how-we-work-mobile.a4de14b0.svg);
  background-repeat: no-repeat;
  background-position: right; 
  background-size: contain;
}

.cta-content {
  width: 50%;
}

.cta-content h1 {
  font-size: 2rem;
}

.cta-content a {
  text-transform: uppercase;
  border: 2px solid #fff; 
  padding: 10px;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}

.cta-content a:hover {
  background-color: #fff; 
  color: #2C2640;
}

.footer {
  width: 100%;
  background-color: #FAFAFA;
  background-image: url(/insure-lp-fem/static/media/bg-pattern-footer-mobile.c33e3a52.svg);
  background-repeat: no-repeat; 
  background-position: left top;

}

.footer-container {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.footer-header {
  display: flex; 
  flex-direction: column;
  align-items: center;
  padding: 50px 0px;
}

.footer-lists {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  text-align: center;
}

.footer-lists li {
  list-style: none;
  margin-bottom: 15px;
}

.footer-lists h3 {
  color: #837D88;
}

.footer-list a {
  color: #2C2640;
  
}
body {
  padding: 0; 
  margin: 0; 
  border-style: border-box;
  font-family: 'karla', sans-serif;
}

h1, h2 {
  font-family: 'dm serif text', serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}
